.parallax {
    /* The image used */
    background-image: url(/feed-the-hungry/static/media/splash-banner.b706150b.jpg);
    /* height: 100%; */
    /* Set a specific height */
    width: 100%;
    height: 660px;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    /* transform: translate(-10%, -10%); */
    background-position-x: -240px;
    /* background-position-y: 100px; */
}
.body-container {
    padding: 0;
    margin-top: 121px;
    height: auto;
    width: 100%;
    /* width: 100%; */
}
.splash-banner {
    width: 100%;
    /* min-width: 980px; */
    min-height: 320px;
    box-sizing: border-box;
}
.splash-banner .columns{
    margin: 0;
    padding: 0;
}
.splash-banner-left {
    width: 100%;
    overflow: hidden;
    height: 660px;
}

.splash-banner-right {
    width: 100%;
    background-color: #009E00;
    height: 660px;
}
.splash-banner-right-content {
    margin-left: 17px;
    min-width: 490px;
    width: 100%;
    height: auto;
}
.splash-banner-title {
    padding: 53px 0px 29px calc((100% - 490px) * 0.5);
    font-size: 72px;
    font-family: 'Raleway',sans-serif;
    color: white;
    text-transform: uppercase;
}
.splash-banner-subtitle {
    margin: 0px 0px 29px calc((100% - 490px) * 0.5);
    font-size: 20px;
    color: white;
    font-family: 'Nunito', sans-serif;
}
.get-food-btn {
    width: 118px;
    font-weight: 300;
    margin: 0px 0px 29px calc((100% - 490px) * 0.5);
    font-family: 'Nunito', sans-serif;
    background-color: white;
}
.get-food-btn:hover{
    border-color:#009E00;
    transition: border-color 0.4s ease 0s, background-color 0.4s ease 0s;
    background-color:  #009E00;
}
.steps {
    width: 100%;
    height: auto;
    margin-bottom: 4.5rem;
}

