/* STEP ITEM */
.step-item {
    width: 100%;
    height: 520px;
}
.steps .level-item {
    flex: 0 1 426.4px;
    min-width: 300px;
    margin-right: 40px;
}
.steps .level-item:last-child {
    margin-right: 0;
}


.step-item-image img{
    width:100%;
    height: 300px;
}
.step-item-content {
    justify-content: space-between;
    overflow: hidden;
    width: 100%;
    min-height: calc(100% - 300px);
    padding-top: 53px;
    padding-left: 32px;
    padding-right: 32px;
    /* padding-bottom: 53px; */
}
