@media only screen and (max-width: 770px) {
    .quotes {
        padding-top: 53px;
    }
    
    .steps .level-item {
        margin-right: 0;
    }
    .steps .level-item:last-child {
        margin-right: 0;
    }
    
    .splash-banner-left, .splash-banner-right {
        height: 390px;
    }

    .splash-banner-right-content {
        overflow-x: auto;
        text-align: center;
        margin-left: 0;
        min-width: 100%;
        height: auto;
    }
    .splash-banner-left-content {
        overflow-x: auto;
    }
    .splash-banner-title {
        font-size: 40px;
        padding-left: 0;
    }
    .splash-banner-subtitle {
        padding-left: 0;
        margin-left: 0;
    }
    .get-food-btn {
        margin-left: 0;
    }
    .step-item-image {
        height: 200px;
    }
    .step-item-content {
        height: 190px;
    }
    .step-title {
        font-size: 18px;
    }
    /* .quotes-content {
